<template>
	<li class="stars-list" style="float: left; list-style: none; position: relative; width: 245px;">
		<a href="javascript:void(0);" :data-pdt-ele="item.id">
			<div class="shuffer-stars">
				<img class="thumb-img thumb-img-lazy" :src="item.src" />
				<div class="cover"></div>
				<div class="shuffer-line"></div>
				<div class="stars-info">
					<div class="name">{{item.name}}</div>
					<div class="intro">{{item.title}}</div>
				</div>
			</div>
		</a>
	</li>
</template>

<script>
	export default {
		props: ['item'],
	}
</script>

<style>

</style>


